.text-serif {
  font-family: $serif-font;
}

.text-serif-bold {
  font-family: $serif-bold-font;
}

.text-sans-light {
  font-family: $sans-serif-light-font;
}

.text-sans-bold {
  font-family: $sans-serif-bold-font;
}

.text-sans-italic {
  font-family: $sans-serif-italic-font;
}

.text-sans-bold-italic {
  font-family: $sans-serif-bold-italic-font;
}

.serif-font-bold {
  font-family: $serif-bold-font;

  .no-fontface & {
    font-weight: 600;
    font-style: italic;
  }
}

.serif-font {
  font-family: $serif-font;

  .no-fontface & {
    font-style: italic;
  }
}

.sans-serif-font-bold-italic {
  font-family: $sans-serif-bold-italic-font;

  .no-fontface & {
    font-weight: 600;
    font-style: italic;
  }

  & strong {
    font-family: $sans-serif-bold-font;
  }
}

.sans-serif-font-bold {
  font-family: $sans-serif-bold-font;

  .no-fontface & {
    font-weight: 600;
  }
}

.sans-serif-font-italic {
  font-family: $sans-serif-italic-font;

  .no-fontface & {
    font-style: italic;
  }
}

.sans-serif-light-font {
  font-family: $sans-serif-light-font;

  .no-fontface & {
    font-weight: 200;
  }
}

.sans-serif-light-font-italic {
  font-family: $sans-serif-italic-font;

  .no-fontface & {
    font-weight: 200;
  }
}

.sans-serif-font {
  font-family: $sans-serif-font;
}

b,
strong {
  @extend .sans-serif-font-bold;
}

i,
em {
  @extend .sans-serif-font-italic;

  font-style: normal;
}

b i,
b em,
strong i,
strong em,
em strong,
em b,
i strong,
i b {
  @extend .sans-serif-font-bold-italic;
}

.text-inverted {
  color: $white;
}

#content p a {
  background: linear-gradient($white, $white), linear-gradient($white, $white), linear-gradient($brand-primary, $brand-primary);
  background-position: 0 95%, 100% 95%, 0 95%;
  background-repeat: no-repeat, no-repeat, repeat-x;
  background-size: .05em 1px, .05em 1px, 1px 1px;
  text-shadow: .03em 0 $white, -.03em 0 $white, 0 .03em $white, 0 -.03em $white, .06em 0 $white, -.06em 0 $white, .09em 0 $white, -.09em 0 $white, .12em 0 $white, -.12em 0 $white, .15em 0 $white, -.15em 0 $white;

  &:hover {
    text-decoration: none;
    background: none;
  }
}

a {
  transition: color .15s;
}

.text-white a:not(.btn) {
  text-decoration: underline;
  color: #dfdbd3;

  &:hover {
    color: darken(#dfdbd3, 15%);
  }
}
